<template>
    <div>
        <a-button-group style="margin-right: 20px">
            <a-button
                    @click="handleGoToIndex"
                    :type="'primary'">
                首页
            </a-button>
            <a-button
                    @click="handleGoBack"
                    :type="'primary'">
                后退
            </a-button>
        </a-button-group>

        <slot>
            <a-button-group v-if="isGroup">
                <template v-for="(button,index) in menu_computed">
                    <a-badge :count="getBadgeCount(button)">
                        <a-button
                                :type="button.hasOwnProperty('type')?button.type:'primary'"
                                :disabled="isDisabled(index)"
                                @click="handleClick(index)">
                            {{button.name}}
                        </a-button>
                    </a-badge>
                </template>
            </a-button-group>
            <template v-else v-for="(button,index) in menu_computed">
                <a-badge :count="getBadgeCount(button)" style="margin-right: 12px">
                    <a-button
                            :type="button.hasOwnProperty('type')?button.type:'primary'"
                            :disabled="isDisabled(index)"
                            @click="handleClick(index)">
                        {{button.name}}
                    </a-button>
                </a-badge>
            </template>
        </slot>

        <slot name="suffix"></slot>
    </div>
</template>

<script>
    import {in_array, empty} from "@/libs/tools";

    export default {
        name: "TopButtons",
        props: {
            menu: {
                required: true,
                type: Array
            },
        },
        data() {
            return {
                routeName: null
            }
        },
        computed: {
            menu_computed() {
                let m = this.menu;
                let result = [];
                for (let i = 0; i < m.length; i++) {
                    let row = m[i];
                    if (!row.hasOwnProperty('auth') || empty(row.auth)) {
                        result.push(row);
                    } else if (in_array(row.auth, this.$store.getters.getAuths)) {
                        result.push(row);
                    }
                }
                return result;
            },
            isGroup() {
                for (let i = 0; i < this.menu.length; i++) {
                    if (!this.menu[i].hasOwnProperty('badge')) {
                        continue;
                    }
                    if (!this.menu[i].badge) {
                        continue;
                    }
                    return false;
                }
                return true;
            },
        },
        methods: {
            getBadgeCount(button) {
                if (!button.hasOwnProperty('badge')) {
                    return 0;
                }
                return button.badge;
            },
            handleGoToIndex() {
                this.$router.push({name: 'pc首页'})
            },
            handleGoBack() {
                history.go(-1)
            },
            isDisabled(index) {
                return this.routeName === this.menu[index].routeName;
            },
            handleClick(index) {
                this.$router.push({name: this.menu[index].routeName})
            },
        },
        watch: {
            $route: {
                handler(val) {
                    this.routeName = val.name;
                },
                immediate: true
            }
        }
    }
</script>

