<template>
    <a-modal
            width="60%"
            v-model="visible"
            :footer="null"
            :destroyOnClose="true"
            @afterClose="handleClose"
            @cancel="handleClose"
    >
        <div slot="title">
            <a-row>
                <a-col :span="1">
                    <img :src="require('@/assets/pc/images/dhico.png')"
                         style="z-index: 9999;height: 18px">
                </a-col>
                <a-col :span="23">
                    {{title}}
                </a-col>
            </a-row>
        </div>

        <a-spin :spinning="spinning">
            {{data}}
        </a-spin>

    </a-modal>
</template>

<script>
    import {getNotification} from '@/api/notification'

    export default {
        props: {
            id: {required: true, type: Number},
            visible: {required: true, type: Boolean},
        },
        model: {
            prop: 'visible',
            event: 'change'
        },
        data() {
            return {
                spinning: true,
                title: '数据加载中。。',
                data: {},
            }
        },
        methods: {
            fetch() {
                this.spinning = true;
                if (this.id === 0) {
                    return;
                }

                getNotification(this.id)
                    .then(res => {
                        this.data = res.data;
                        this.spinning = false;
                    })
            },
            handleClose() {
                this.$emit('change', false);
            },
        },
        watch: {
            visible(newVal, oldVal) {
                if (newVal) {
                    this.fetch();
                } else {
                    this.title = '数据加载中。。';
                    this.data = [];
                }
            }
        }
    }
</script>

