<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <div class="columns bac_white">
            <table-component
                    style="width: 100%"
                    ref="table"
                    template="notification"
                    @handleOperate="handleOperate"
                    :columns="columns"
                    :request="tableRequest">
            </table-component>
        </div>
        <CheckModal v-model="modalVisible" :id="checkId"/>
    </div>
</template>

<script>
    import TopButtons from './components/TopButtons'
    import tableComponent from '@/components/table'
    import CheckModal from './components/CheckModal'
    import {getUserNotificationList} from '@/api/notification'

    export default {
        name: "Index",
        components: {
            TopButtons, tableComponent, CheckModal
        },
        props: {
            type: {
                required: true,
                type: [Number, String],
            },
        },
        computed: {
            type_computed() {
                return parseInt(this.type);
            }
        },
        data() {
            return {
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: 'user_id',
                        dataIndex: 'user_id',
                        key: 'user_id',
                    },
                    {
                        title: 'is_checked',
                        dataIndex: 'is_checked',
                        key: 'is_checked',
                    },
                    {
                        title: 'related_type',
                        dataIndex: 'related_type',
                        key: 'related_type',
                    },
                    {
                        title: 'related_id',
                        dataIndex: 'related_id',
                        key: 'related_id',
                    },
                    {
                        title: 'operate_type',
                        dataIndex: 'operate_type',
                        key: 'operate_type',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {visible: true, disable: false,},
                        }
                    }
                ],
                tableRequest: getUserNotificationList,
                modalVisible: false,
                checkId: 0,
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'check':
                        this.handleCheck(row);
                        break;
                }
            },
            handleCheck(row) {
                this.checkId = row.id;
                this.modalVisible = true;
            },
        },
        mounted() {
            this.handleSearch({related_type: this.type_computed});
        },
        watch: {
            type_computed: {
                handler(val) {
                    this.handleSearch({related_type: val});
                },
            },
            modalVisible(val) {
                if (!val) {
                    this.handleSearch({related_type: val});
                }
            }
        }
    }
</script>

