import axios from '@/libs/api.request'
import store from '@/store'

const baseUrl = 'user_notifications/';

export const getNotificationList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

export const getUserNotificationList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter: Object.assign(filter, {user_id: store.state.common.user_id}),
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

export const getNotification = (id) => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'get',
        params: data,
    })
}

export const getUserUnreadNotificationBadgeCount = () => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + store.state.common.user_id + '/unread_badge_count',
        method: 'get',
        params: data,
    })
}

export const createNotification = (d) => {
    const data = {
        data: Object.assign({user_id: store.state.common.user_id}, d),
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl,
        method: 'post',
        data,
        params,
    })
}

export const updateNotification = (id, d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'put',
        data,
        params,
    })
}

export const deleteNotification = (id) => {
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'delete',
        params,
    })
}

