<template>
    <div>
        <buttonMenu :menu="menu">

            <template v-for="(button,index) in menu">
                <a-badge :count="getBadgeCount(button)" style="margin-right: 12px">
                    <a-button
                            :type="button.hasOwnProperty('type')?button.type:'primary'"
                            @click="handleClick(button)">
                        {{button.name}}
                    </a-button>
                </a-badge>
            </template>

        </buttonMenu>
    </div>
</template>

<script>
    import buttonMenu from '@/components/buttonMenu/Index'
    import {getUserUnreadNotificationBadgeCount} from '@/api/notification'

    export default {
        name: "TopButtons",
        components: {buttonMenu},
        computed: {
            pushSignal() {
                return this.$store.state.pc.pushSignal;
            }
        },
        data() {
            return {
                menu: [],
            }
        },
        methods: {
            init() {
                getUserUnreadNotificationBadgeCount()
                    .then(res => {
                        let map = res.data.map;
                        let count = res.data.count;

                        let menu = [];

                        let sum = 0;
                        for (let type in map) {
                            let name = map[type];
                            let badge = count.hasOwnProperty(type) ? count[type] : 0;
                            sum += badge;
                            menu.push({
                                name: name, routeName: '通知列表', 'badge': badge, params: {type: type},
                            });
                        }

                        this.menu = [].concat(
                            [{name: '全部通知', routeName: '通知列表', 'badge': sum, params: {type: 0}}],
                            menu
                        );
                    });
            },
            getBadgeCount(button) {
                if (!button.hasOwnProperty('badge')) {
                    return 0;
                }
                return button.badge;
            },
            handleClick(button) {
                this.$router.push({name: button.routeName, params: button.params})
            },

        },
        watch: {
            pushSignal: {
                handler(val) {
                    this.init();
                },
                immediate: true
            }
        }
    }
</script>

